.head-wrapper {
  background: var(--main-color);
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1080px;
  margin: 0 auto;
  padding: 10px 0;
  color: #fff;
}

.logo {
  display: flex;
  align-items: center;
}

.logo > a {
  color: #fff;
}

h1 {
  font-size: 18px;
  font-family: 'Playfair Display', 'Georgia', serif;
}

.back-link {
  padding: 2px 10px;
  color: #fff;
  font-size: 18px;
  font-family: 'Playfair Display', 'Georgia', serif;
}

.back-link:hover {
  border-bottom: 1px solid #fff;
}

.head-search {
  padding: 2px 10px;
  color: #fff;
  font-size: 18px;
  font-family: 'Playfair Display', 'Georgia', serif;
}

.head-search:hover {
  border-bottom: 1px solid #fff;
}

.header:hover {
  border-bottom: 2px solid #fff;
}

.fa-search {
  padding-right: 8px;
}

.language-select {
  padding: 5px;
  margin-left: 20px;
  color: #fff;
  border: 1px solid #fff;
  background-color: rgba(255, 255, 255, 0.3);
}

.language-select option {
  background-color: transparent !important;
  border: 1px solid #e4e4e4;
  color: #000;
  -webkit-appearance: none;
  -moz-appearance: none;
}

@media screen and (max-width: 1080px){
  .main-header {
    padding: 5px 0;
  }
  .logo {
    padding-left: 10px;
  }
  .nav {
    padding-right: 10px;
  }
}

@media screen and (max-width: 600px){
  .main-header {
    flex-direction: column;
  }
}

.about-container {
  min-height: 680px;
  background: rgba(0, 0, 0, 0.7) url(/static/media/about-1x.4ef53e7f.jpg) top no-repeat;
  background-blend-mode: darken;
  overflow: hidden;
}

.about-wrapper {
  max-width: 1080px;
  margin: 0 auto;
  padding-top: 105px;
  align-items: center;
  color: #fff;
}

h2 {
  padding: 10px 0;
  font-size: 24px;
}

@media screen and (max-width: 1080px){

  .about-container {
    min-height: 600px;
    background-position: center;
  }

  .about-wrapper {
    padding: 80px 25px;
  }

  h2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 600px){
  .about-container {
    min-height: 300px;
    background: rgba(0, 0, 0, 0.7) url(/static/media/about-2x.99a85f4a.jpg) top no-repeat;
  }

  .about-wrapper{
    padding: 60px 5px;
    font-size: 16px;
  }

  h2 {
    font-size: 18px;
  }
}

@media screen and (max-width: 450px){
  .about-container {
    min-height: 200px;
    background: rgba(0, 0, 0, 0.7) url(/static/media/about-3x.25f6178b.jpg) top no-repeat;
  }

  .about-wrapper{
    padding: 20px 5px;
    font-size: 13px;
  }

  h2 {
    font-size: 18px;
  }
}
.director-day {
  display: flex;
  margin: 60px 0;
}

.director-widget {
  display: flex;
  margin: auto;
  background-color: #e4e4e4;
}

.director-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.director-block:last-child {
  align-items: center;
}

.director-widget-heading {
  padding: 10px 60px;
  background-color: var(--main-color);
  color: #fff;
  text-transform: uppercase;
  font-family: 'Playfair Display', 'Georgia', serif;
  font-size: 22px;
}

h4 {
  padding: 10px 0;
  color: #585757;
  font-size: 18px;
  font-family: 'Noto Serif SC', 'Georgia', serif;
}

.director-info {
  width: 300px;
  padding: 5px;
  margin-bottom: 10px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 14px;
  color: #6f6e6e;
}

.director-widget-button {
  display: inline-block;
  padding: 15px;
  color: var(--main-color);
  background-color: #fff;
  border: 1px solid var(--main-color);
  font-family: 'Noto Serif SC', serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.director-widget-button:hover {
  color: #fff;
  background-color: var(--main-color);
}

.director-widget img{
  max-width: 100%;
  flex-grow: 0;
}

@media screen and (max-width: 600px){
  .director-widget {
    flex-direction: column;

  }

  .director-widget:first-child {
     align-items: center;

   }

  .director-widget-heading {
    padding: 5px 15px;
    font-size: 20px;
    align-self: stretch;
  }

  h4 {
    font-size: 14px;
  }

  .director-block {
    align-items: flex-start;
  }

}
.team-block {
  background-color: var(--main-color);
  margin-top: 30px;
  padding: 30px 0;
}

.team-wrapper {
  margin: 0 auto;
  max-width: 1080px;
}

.team-heading {
  color: #fff;
}

.team {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.team-member {
  display: inline-block;
  margin-top: 10px;
  color: #fff;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 20px;
}

.team-member-photo {
  border-radius: 50%;
  max-width: 100%;
}

h3 {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 32px;
  text-transform: uppercase;
}

@media screen and (max-width: 500px){
  .team {
    flex-direction: column;
  }

  .team-member-photo {
    width: 100px;
  }
}
footer {
  background: #2a2a2a;
  flex-shrink: 0;
}

.foot-wrapper{
  max-width: 1080px;
  margin: 0 auto;
  padding: 10px 0;
  color: #fff;
}

.foot-main{
  display: flex;
  justify-content: center;
  margin: 15px 0;
  align-items: center;
}

.block-string {
  padding-left: 10px;
  font-size: 18px;
  font-family: 'Open Sans Condensed', 'Georgia', serif;
  color: #fff;
}

.fa-search {
  padding-right: 8px;
}

.copyright {
  font-family: 'Poiret One', cursive;
  text-align: center;
  margin: 15px 0;
}
:root {
  --main-color: #f44242;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.main-wrapper {
  flex: 1 0 auto;
  text-align: center;
}

.search-wrapper {
  padding: 30px 0;
}

.search-heading {
  font-family: 'Noto Serif SC', serif;
  font-size: 20px;
  text-align: center;
}

.search-bar {
  display: flex;
  flex-direction: column;
  width: 46%;
  height: 30px;
  text-align: center;
  font-size: 23px;
  margin: 5px auto 30px auto;
  text-transform: capitalize;
}

.search-bar:focus {
  outline-color: #f44242;
  outline-color: var(--main-color);
}

.director-data {
  display: flex;
  border: 1px solid lightgray;
  max-width: 1080px;
  margin: 10px auto;
  padding: 15px;
  overflow: hidden;
}

.director-data-img {
  margin-right: 20px;
}

.director-data-bio {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.director-data-bio > div {
  font-family: 'Noto Serif SC', 'Georgia', serif;
}

.director-data-city {
  font-size: 14px;
  font-style: italic;
}

.director-data-info {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 14px;
  color: #6f6e6e;
}

.director-data-button {
  display: inline-block;
  padding: 5px;
  color: #f44242;
  color: var(--main-color);
  background-color: #fff;
  border: 1px solid #f44242;
  border: 1px solid var(--main-color);
  font-family: 'Noto Serif SC', serif;
  font-size: 16px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.director-data-button:hover {
  color: #fff;
  background-color: #f44242;
  background-color: var(--main-color);
}

.director-page {
  max-width: 1024px;
  margin: 10px auto;
}

.director-page-img {
  display: inline-block;
  width: 50%;
}

.director-page-img img{
  display: block;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.director-page-bio {
  display: inline-block;
  width: 50%;
  vertical-align: top;
  text-align: center;
  margin-top: 10%; 
}

.director-page-bio > h1{
  font-size: 1.8rem;
}

.director-page-name {
  font-size: 50px;
  font-family: 'Noto Serif SC', 'Georgia', serif;
  margin-bottom: 20px;
}
.director-page-city {
  font-size: 18px;
  font-style: italic;
  font-family: 'Noto Serif SC', 'Georgia', serif;
}

.carousel-photo {
  height: 400px;
  margin: 0 20px;
}

div.bigpicture.link.picture {
  display: block !important;
  margin: 50px auto;
}

.director-video {
  display: block;
  margin: 0 auto;
}

.carousel {
  overflow-x: hidden;
}

@media only screen and (max-width: 600px) {
  .director-page-img {
    display: block;
    width: 100%;    
  }

  .director-page-img img {
    width: 60%;
  }

  .director-page-bio {
    display: block;
    width: 100%;
  }

  .carousel-photo {
    height: 200px;
    margin: 0 20px;
  }

  .director-video {
    width: 100%;
  }

  .director-page-name {
    font-size: 20px;
    font-family: 'Noto Serif SC', 'Georgia', serif;
    margin-bottom: 5px;
  }

  .director-page-city {
    margin-bottom: 25px;
  }
}
